@import '../../scssMixin/generateClassName';

// clasName

@include z('text-center',$global:true){//
    text-align: center;
}
@include z('text-left',$global:true){//
    text-align: left;
}
@include z('text-right',$global:true){//
    text-align: right;
}
@include z('text-underline',$global:true){//
    text-decoration: underline;
}
@include z('text-underline-hover',$global:true){// hover
    &:hover{
        text-decoration: underline;
    }
}
@include z('vertical-top',$global:true){//
    vertical-align: top;
}
@include z('vertical-middle',$global:true){//
    vertical-align: middle;
}
@include z('vertical-bottom',$global:true){//
    vertical-align: bottom;
}