@import '../../scssMixin/generateClassName';
//12-100
//"-imprtant"

@mixin setSizes($size){
    @include z('font-size-'+$size,$global:true){
        font-size: $size+px;
    }
    @include z('font-size-'+$size+'important',$global:true){
        font-size: $size+px!important;
    }
}
// 12-100px className  :   z-font-size-12  
@for $i from 12 through 100 {
    @include setSizes($i);
}